import {useEffect, FC} from "react";
import { useTranslation } from "react-i18next";

import InfoBlock from "../InfoBlock/InfoBlock";
import { convertJSONToText } from "../../helpers/convertJSONToText";
import { IInfoBlocks, IJSONText } from "../GeneralInfoTest/GeneralInfoTest";

const GeneralInfoTest: FC = () => {
    const { t } = useTranslation();
    const dataReportBug: IInfoBlocks[] = [
      {
        title: t(""),
        img: require("./images/mistake.jpg"),
        alt: "     ",
        id: "reportBugText",
        text: t("___"),
      },
    ];
    const blockReportBug = dataReportBug.map((block, index) => (
      <InfoBlock
        key={index+1}
        title={block.title}
        text={block.text}
        imgSrc={block.img || ""}
        imgAlt={block.alt || ""}
        idForText={block.id}
      />
    ));
    
      const JSONText: IJSONText[] = [
        { selector: "#reportBugText", json: t("___") },
      ];

      useEffect(() => {
        convertJSONToText(JSONText);
      });


  return (
    <section>
      {blockReportBug}
    </section>
  );
};

export default GeneralInfoTest;
